import Planner from './components/Planner.vue';
import Home from './components/Home.vue';

import Rijksmuseum from './components/Rijksmuseum.vue';

export const routes = [
	{ path: '/', component: Home },
	{ path: '/planner', component: Planner },
	{ path: '/rijksmuseum', component: Rijksmuseum }
];


