<script>
export default {
  data: function() {
      return {
          name: 'aaa',
          modus: 'out-in'
      }
  }, 
}
</script>

<style lang="scss">

$c-blue : #304ffe;

html, body {
    padding: 0;
    margin: 0;
    line-height: 1.5;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2 {
  font-weight: normal;
  font-size: 2em;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  text-decoration: none;
  color: $c-blue;
  font-weight: bold;
}


// transitions 

.aaa-leave { 

}

.aaa-leave-active { 
  animation: leaver 0.5s forwards;
}

.aaa-enter {

}

.aaa-enter-active { 
  
}

@keyframes leaver {
  0% {

  }
  100% {
      transform: translateX(-150px);
      opacity: 0;
  }
}
</style>



